@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
body{margin:0;font-family:Work Sans,sans}a{cursor:url(data:image/png;base64,) -30 -30,auto}


.header{width:100%;display:flex;flex-direction:row;justify-content:center;align-items:center;height:10vh;background-color:white;font-weight:600}.header .logo{height:7vh;padding-left:10px;padding-right:10px}.header .language-switch{border:none;font-family:Work Sans,sans;background-color:white;font-weight:600;font-size:inherit}

.nav{display:flex;flex-direction:row;justify-content:space-between;align-items:center;height:10vh;padding:0 40px}.nav a{color:black;text-decoration:none;font-weight:600}

.home{height:80vh;display:flex}.home .link{height:inherit;width:50%;color:white;font-weight:600;text-decoration:none;display:flex;align-items:center;padding:30px}.home .stories{background-image:url(/act-of-benevolence/static/media/stories.8f385d2e.jpg);background-size:cover;background-position:center;justify-content:flex-end}.home .tshirts{background-image:url(/act-of-benevolence/static/media/tshirts.058a5675.jpg);background-size:cover;background-position:center}

.overview{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;width:100%;height:100%;grid-gap:2.5%}.overview>*{width:20vw}.overview .element{display:inline-block;overflow:hidden;position:relative;width:100%;height:30vh}.overview .element img{height:100%;width:100%;object-fit:contain}.overview .element .main-image{transition:opacity 0.5s, opacity 0.2s linear}.overview .element .main-image:hover{opacity:0}.overview .element .main-image:hover+.secondary-image{opacity:100%}.overview .element .secondary-image{object-fit:cover;object-position:center;position:absolute;top:0;left:0;z-index:-1;opacity:0;transition:opacity 0.2s, opacity 0.5s linear}.overview .list{list-style-type:none}

.details-view{margin:40px;display:grid;grid-template-columns:1fr 1fr;grid-gap:40px;height:80vh}.details-view img{height:70vh}.details-view h2{margin:5px 0 5px 0}.details-view .alignTextRight{text-align:right}.details-view .horizontal-line{height:3px;margin-top:1rem;border-bottom:1px solid black}

